.navbar {
  background-color: #ebb;
}

.navbar .brand {
  padding: 0;
}

.navbar .brand h4 {
  font-size: 3rem;
  font-weight: 300;
  color: #fff8;
  margin: 0;
}

/*Addresses area around nav items*/
.navbar .nav-link {
  margin: 0 1rem 0 1rem;
  padding: 0;
  font-weight: 300;
  font-size: 1.4rem;
}

/*Addresses actual text of nav items, allows text colour to be set*/
.nav-link h4 {
    margin-top: 1px;
  color: white;
  font-weight: 300;
}

.active h4 {
  color: #fff8;
}

#uni-dropdown {
  color: white;
}

#non-uni-dropdown {
  color: white;
}