@import url(https://fonts.googleapis.com/css2?family=Jost:wght@200;300;400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*body {
  font-family: "Jost", sans-serif;
  background-color: #FCC;
  margin: 0;
  margin-bottom: 40px;
}*/


.App {
  text-align: center;
  font-family: "Jost", sans-serif;
}

.App-header {
  background-color: #FCC;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  /*font-size: calc(10px + 2vmin);*/
  color: rgb(116, 101, 101);
}

/*END OF APP CSS*/

.fadein {
  -webkit-animation-name: fadein;
          animation-name: fadein;
}

.intro {
  color: white;
  text-align: center;
}

.App .App-header h1 {
  font-size: 10rem;
}

.App .App-header h2 {
  font-size: 5rem;
}

.App .App-header h3 {
  font-size: 3rem;
}

p {
  font-size: 4rem;
}

/* OLD P, FOR PARAGRAPHS
p {
  margin: 0 1.5rem 0 1.5rem;
  color: #666;
  font-size: 1.3rem;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}*/

.as1 {
  -webkit-animation-duration: 1.5s;
          animation-duration: 1.5s;
}

.as2 {
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
}

.as3 {
  -webkit-animation-duration: 4.5s;
          animation-duration: 4.5s;
}

.as4 {
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
}

.main-content {
  background-color: white;
  border-radius: 20px;
  margin: 40px 10px 0 10px;
}

.heading {
  text-align: center;
  padding-top: 5px;
  color: #666;
  font-size: 2.3rem;
}

.caption {
  text-align: center;
  font-size: 1rem;
}

.centered-pic {
  margin: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.navbar-toggler,
.navbar-toggler-icon {
  background-color: white;
}

.active {
  color: #fff8;
}

.navbar {
  background-color: #ebb;
}

.navbar .brand {
  padding: 0;
}

.navbar .brand h4 {
  font-size: 3rem;
  font-weight: 300;
  color: #fff8;
  margin: 0;
}

/*Addresses area around nav items*/
.navbar .nav-link {
  margin: 0 1rem 0 1rem;
  padding: 0;
  font-weight: 300;
  font-size: 1.4rem;
}

/*Addresses actual text of nav items, allows text colour to be set*/
.nav-link h4 {
    margin-top: 1px;
  color: white;
  font-weight: 300;
}

.active h4 {
  color: #fff8;
}

#uni-dropdown {
  color: white;
}

#non-uni-dropdown {
  color: white;
}
