.App {
  text-align: center;
  font-family: "Jost", sans-serif;
}

.App-header {
  background-color: #FCC;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  /*font-size: calc(10px + 2vmin);*/
  color: rgb(116, 101, 101);
}

/*END OF APP CSS*/

.fadein {
  animation-name: fadein;
}

.intro {
  color: white;
  text-align: center;
}

.App .App-header h1 {
  font-size: 10rem;
}

.App .App-header h2 {
  font-size: 5rem;
}

.App .App-header h3 {
  font-size: 3rem;
}

p {
  font-size: 4rem;
}

/* OLD P, FOR PARAGRAPHS
p {
  margin: 0 1.5rem 0 1.5rem;
  color: #666;
  font-size: 1.3rem;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
}*/

.as1 {
  animation-duration: 1.5s;
}

.as2 {
  animation-duration: 3s;
}

.as3 {
  animation-duration: 4.5s;
}

.as4 {
  animation-duration: 5s;
}

.main-content {
  background-color: white;
  border-radius: 20px;
  margin: 40px 10px 0 10px;
}

.heading {
  text-align: center;
  padding-top: 5px;
  color: #666;
  font-size: 2.3rem;
}

.caption {
  text-align: center;
  font-size: 1rem;
}

.centered-pic {
  margin: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  75% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.navbar-toggler,
.navbar-toggler-icon {
  background-color: white;
}

.active {
  color: #fff8;
}
